<template>
  <div class="card mt-5" style="width: 500px">
    <div class="card-body login-card-body">
      <h3 class="login-box-msg"></h3>
      <form>
        <div class="card-body">
          <div class="form-group">
            <label for="username"></label>
            <input
              v-model="user.username"
              type="text"
              min="1"
              class="form-control"
              id="username"
              placeholder=""
            />
          </div>
          <div class="form-group">
            <label for="password"></label>
            <input
              v-model="user.password"
              v-on:keyup.enter="signIn()"
              type="password"
              class="form-control"
              id="password"
              placeholder=""
            />
          </div>
          <div class="social-auth-links text-center mb-3 mt-5">
            <!-- <a href="register"><button type="button" class="btn btn-secondary"></button></a>
            &nbsp; -->
            <button @click="signIn()" type="button" class="btn btn-success"></button>
          </div>
        </div>
      </form>
    </div>
  </div>
</template>
<script src="https://raw.githubusercontent.com/biggora/device-uuid/master/lib/device-uuid.min.js"></script>

<script>

export default {
  name: "login",
  components: {},
  data() {
    return {
      user: [],
    };
  },
  computed: {
    loggedIn() {
      return this.$store.state.auth.status.loggedIn;
    },
    currentUser() {
      return this.$store.state.auth.user;
    },
  },
  methods: {   
     signIn() {
      if (this.user.username == "" || this.user.username == null) {
        alert("");
      } else if (this.user.password == "" || this.user.password == null) {
        alert("");
      } else {
        var user = {
          username: this.user.username,
          password: this.user.password,
        };
        this.$store.dispatch("auth/login", user).then(
          () => {
            this.loading = true;
            setTimeout(function () {
              location.reload();
              
            this.$router.push("/maps");
            }, 500);
          },
          (error) => {
            alert("");
            console.log(error);
            // this.loading = false;
            // this.message =
            //   (error.response &&
            //     error.response.data &&
            //     error.response.data.message) ||
            //   error.message ||
            //   error.toString();
          }
        );
      }
    },
    
  },
  mounted() {
    if (this.currentUser) {
      this.$router.push("/maps");
    }
    },
};
</script>

